<template>
	<section class="page-wrap">
		<app-header/>
		<header class="section-masthead">
			<figure class="section-masthead__media">
				<lazy-image :src="imgSrc" :alt="imgAlt"/>
			</figure>
			<section class="section-masthead__text">
				<h1>Tempo</h1>
				<p class="subtitle">Making a tissue brand the perfect ally for the most emotional moments</p>
			</section>
		</header>
		<article class="work">
			<section class="work__detail">
				<h3 class="work__subtitle">Context</h3>
				<p>
					To reaffirm its leadership in the tissue market and jusify its premium price against private labels,
					Tempo faced the challenge of de-stagionalize its products and change a brand perception that was
					functional and linked exclusively to cold&fu or allergies.
				
				</p>
			</section>
			<section class="work__detail">
				<h3 class="work__subtitle">Insight</h3>
				<p>
					Italy is one of the most passionate and «emotional» countries on Earth, and we love to share our
					feeling with people around us.
				</p>
				<p>
					Tempo tissues are mostly associated to cold&flu and allergies but many times we are in need of a
					tissue when we’re happy about somethig and feel very emotional.
				</p>
				<p>
					We realized that Tempo could play a different role in the category by becoming the ally of emotions
					in people’s lives.
				</p>
			</section>
			<section class="work__detail">
				<h3 class="work__subtitle">Creative idea</h3>
				<p>
					To demonstrate the being in need of a tissue could be the best thing in someone’s life we launched
					an online hero video called “Dream proposal” about the most romantic and unusual marriage proposal
					ever.
				</p>
				<p>
					The online video was followed by extra “making of” videos and related content for social media.
				</p>
			</section>
			<section class="work__detail">
				<h3 class="work__subtitle">Impact</h3>
				<p>
					Tempo reaffirmed its leadership in the marketand gained 2 points MS vs main competitor Kleenex.
				</p>
				<ul>
					<li>13M views combined</li>
					<li>127M impressions</li>
					<li>463k clicks on the video</li>
				</ul>
				<p>
					Dream proposal had 2 shortlists at Cannes Lions 2016.
				</p>
				<p>
					Won best use of YouTube at IAB 2016.
				</p>
			</section>
		</article>
		<video-wrapper src="//www.youtube.com/embed/LADLAhA7hkM"
		               title="Bayer - Aspirin C"/>
		<app-footer/>
	</section>
</template>

<script>

	import LazyImage from './LazyImage';
	import AppHeader from './AppHeader';
	import AppFooter from './AppFooter';
	import VideoWrapper from './VideoWrapper';

	export default {
		name: 'Tempo',
		components: {
			LazyImage,
			AppHeader,
			AppFooter,
			VideoWrapper,
		},
		data() {
			return {
				imgSrc: '/static/philadelphia.jpg',
				imgAlt: 'Philadelphia'
			}
		}
	}
</script>

<style scoped>
	.section-masthead {
		height: 100vh;
		display: flex;
		align-items: center;
		margin-top: 2rem;
	}
	
	.section-masthead__text {
		padding: 1em 1.5em;
		background-color: var(--color__dark-alpha);
		transform: translateX(-10%);
	}
	
	.section-masthead__media {
		height: 100%;
		position: relative;
	}
	
	.section-masthead__media::after {
		content: '';
		position: absolute;
		top: -7px;
		left: 7px;
		width: 100%;
		height: 101%;
		background-color: var(--color__complementary);
		opacity: 0.5;
		mix-blend-mode: multiply;
		transform: rotate(0.5deg);
	}
	
	img {
		height: 100%;
		display: block;
		width: 100%;
		object-fit: cover;
	}
	
	h1 {
		font-size: 5vw;
	}
	
	p {
		margin-bottom: 0;
	}
	
	article {
		margin-top: 2em;
		margin-bottom: 2em;
	}
	
	.work__detail {
		border: 1px solid var(--color__dark-alpha);
		padding: 0.75em 1.25em;
	}
	
	.work__subtitle {
		background-color: var(--color__light);
		padding: 0 0.5em;
		transform: translateY(-1.5em);
		margin: 0;
		display: inline-block;
	}
	
	.work__subtitle + p {
		transform: translateY(-1.5em);
	}
	
	ul {
		margin: 0;
		list-style: none;
		padding-left: 0;
	}
	
	li {
		position: relative;
		padding-left: 1em;
	}
	
	li:not(:first-child) {
		margin-top: 0.75em;
	}
	
	li::before {
		content: '-';
		transform: scaleX(3) rotate(20deg);
		position: absolute;
		left: 0;
		top: 0;
	}
	
	@media screen and ( max-width: 64em ) {
		.section-masthead {
			margin-right: -3vw;
			margin-left: -3vw;
		}
		
		.work__detail:not(:first-child) {
			margin-top: 2em;
		}
	}
	
	@media screen and ( min-width: 64em ) {
		h1 {
			font-size: 3vw;
		}
		
		article {
			display: flex;
			background: linear-gradient(135deg, rgba(230, 230, 230, 0.2) 50%, var(--color__light-alpha-norm) 50%) no-repeat 0 -65%;
			background-size: 110% 110%;
			padding: 2em 0 0.5em 0.5em;
		}
		
		.work__detail {
			flex: 1;
		}
		
		.work__detail:not(:first-child) {
			margin-left: 1em;
		}
	}
</style>



